.loadingZone{width:400px;height:400px}.lumiFish{animation-name:lumiAnim;animation-duration:4s;animation-iteration-count:infinite;left:50%;top:60%;position:absolute;width:300px;transform:translate(-50%, -50%);background:url(../../assets/Lumi_Logo.svg) 0 0 no-repeat transparent;height:400px;overflow:hidden;color:#ffffff}@keyframes lumiAnim{0%{width:240px;height:240px;-webkit-filter:drop-shadow(3px 3px 2px black);filter:drop-shadow(3px 3px 2px black);top:50%}50%{width:400px;height:400px;-webkit-filter:drop-shadow(10px 10px 10px black);filter:drop-shadow(10px 10px 10px black);top:45%}100%{width:240px;height:240px;-webkit-filter:drop-shadow(3px 3px 2px black);filter:drop-shadow(3px 3px 2px black);top:50%}}
